import { isObject, isString } from '@guardian/libs';
const isParticipations = (participations) => isObject(participations) &&
    Object.values(participations).every((participation) => isObject(participation) && isString(participation.variant));
const runnableTestsToParticipations = (runnableTests) => runnableTests.reduce((participations, { id: testId, variantToRun }) => ({
    ...participations,
    ...{
        [testId]: {
            variant: variantToRun.id,
        },
    },
}), {});
export { isParticipations, runnableTestsToParticipations };
